<template>
<div class="content">
  <div class="box">
    <div class="left">
      <div class="img">
        <img :src="userinfo.img_url" alt="">
      </div>
      <div class="user">
        <h1>{{userinfo.nickname}}<span style="font-size: 0.6em;margin-left: 5px;color: red">VIP:{{userinfo.vip}}</span></h1>
        <p class="email">{{ userinfo.username }}</p>
        <p class="a" style="color: cornflowerblue;cursor: pointer" @click="flag = true">  ></p>
      </div>
    </div>
    <div class="right">
      <p style="display: flex;align-items: flex-end">
        M
        <img @click="isShow=!isShow" v-show="!isShow" style="margin-left: 2%" src="./images/eye.png">
        <img @click="isShow=!isShow" v-show="isShow" style="margin-left: 2%" src="./images/eye_none.png">
      </p>
      <div class="residual">
        <h1 v-show="isShow" style="font-size: 1.5em;margin-top: 8%;">{{userinfo.residual}}</h1>
        <h1 v-show="!isShow" style="font-size: 1.5em;margin-top: 8%;color: #ff7900">****</h1>
        <div class="button"></div>
      </div>
      <p class="target a" @click="$bus.$emit('flag_page','money')">M  ></p>
    </div>
  </div>
  <div class="zz" v-if="flag">
    <div class="password_box">
      <img class="password_img" src="./images/no.png" alt="" @click="flag=false">
      <div class="input_array">
        <p><span></span><input type="text" v-model="old_password"></p>
        <p><span></span><input type="text" v-model="new_password" @blur="password_revise"></p>
        <p><span></span><input type="text" v-model="affirm_password"></p>
        <p v-if="ifFlag" class="ts" v-text="text"></p>
      </div>
      <div class="button" style="margin-right: 0;margin-top: 15px" @click="submit_password"></div>
    </div>
  </div>
  <onlineshopping></onlineshopping>
</div>
</template>

<script>
import onlineshopping from '../OnlineShopping'
import {reqRevise_password} from "@/api";

export default {
  name: "index",
  props:{
    userinfo:{
      type:Object
    }
  },
  data(){
    return{
      //
      isShow:false,
    //  
      flag:false,
     // ,
      ifFlag:false,
      text:'',
     // 
      old_password:'',
      new_password:'',
    //  ,
      affirm_password:''
    }
  },
  methods:{
  //  
    password_revise(){
      console.log(1)
    },
  //  
    async submit_password(){
      //
      if(this.new_password!==this.affirm_password){
        this.ifFlag = true
        this.text = ''
        return 0
      }
      //
      const result = await reqRevise_password(this.old_password,this.affirm_password)
      console.log(result.data)
      if(result.data.status===0){
        this.flag = false
        alert('')
      }else if(result.data.message===''){
        this.isFlag=true
        this.text = ''
      }else if(result.data.message.password){
        this.ifFlag=true
        this.text = '8-16'
      }
    }
  //  
  },
  components:{
    onlineshopping
  }
}
</script>

<style scoped>
.box{
  width: 100%;
  min-width: 600px;
  display: flex;
  aspect-ratio: 16/3.5;
  margin-bottom: 20px;
}
.left,
.right{
  background-color: white;
  border-radius: 5px;
  display: flex;
  padding: 20px 0 0 30px;
}
.left{
  width: 55%;
  margin-right: 5%;
}
.img{
  width: 100px;
  height: 100px;
  object-fit: cover;
}
.img>img{
  width: 100%;
}
.user{
  margin: 4% 0 0 6%;
}
.user .email{
  height: 25px;
  line-height: 23px;
  margin-top: 10%;
  margin-bottom: 10%;
  padding-left: 22px;
  background: url("./images/email.png") no-repeat 0 0;
  background-size: 21px 21px;
}
.target{
  margin-top: 10%;
  color: cornflowerblue;
  cursor: pointer;
}
.residual{
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.button{
  margin-right: 20px;
  width: 100px;
  height: 30px;
  color: white;
background-color: #6495ef;
  border-radius: 20px;
  font-size: 1.1em;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.right{
  width: 40%;
  flex-direction: column;
}
.a:hover{
  text-decoration: underline #6495ef;
}
.zz{
  position: fixed;
  background-color: rgba(211,211,211,0.5);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.password_box{
  position: absolute;
  height: 200px;
  width: 30%;
  padding: 10px 0;
  max-width: 60%;
  top: calc(50% - 100px);
  left: calc(35%);
  border-radius: 10px;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.password_img{
  position: absolute;
  right: 15px;
  top: 10px;
}
.input_array{
  width: 80%;
  height: 60%;
  display: flex;
  margin-right: 10%;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.input_array p input{
  border: 0;
  border-bottom: 1px solid rgb(211,211,211);
  width: 0;
  flex: 2;
}
.input_array p{
  width: 100%;
  display: flex;
}
.input_array p span{
  font-family: " Bold";
  font-weight: 700;
  flex: 1;
  text-align: right;
}
.ts{
  position: absolute;
  bottom: 25%;
  left: 0;
  font-size: 0.8em;
  color: #b42606;
  justify-content: center;
}
</style>