//post blurb money time
export const reqPay_no_order =(order)=>{
    return request({
        url:'pay/add_order',
        method:'post',
        data:{
            blurb:order.blurb,
            money:order.money,
            time:order.time
        }
    })
}
//
export const reqPay_order = (order) =>{
    return request({
        url:'pay/pay_order',
        method:'post',
        data:{
            id:order.id,
            balance:order.balance,
            courier:order.courier,
            site:order.site,
            note:order.note,
            name:order.name,
            tel:order.tel,
            coupon:order.coupon,
            create_time:order.create_time
        }
    })
}